<template>
  <!--è¡¨å¤´åç»çç¶çº§-->
  <el-table-column v-if="column.children" :label="column.label">
    <ElTableWrapColumn
      v-for="item in column.children"
      :key="item.key || item.prop"
      :column="item"
    />
  </el-table-column>
  <!--åæèªå®ä¹æ¸²æ-->
  <el-table-column
    v-else-if="column.render"
    width="300px"
    :prop="column.prop"
    :label="column.label"
    v-bind="columnConfig"
  >
    <template slot-scope="scope">
      <ColumnSlot :render="column.render" :scope="scope" :column="column" />
    </template>
  </el-table-column>
  <!--é»è®¤æ¸²æ-->
  <el-table-column
    v-else
    :prop="column.prop"
    :label="column.label"
    v-bind="columnConfig"
  >
  </el-table-column>
</template>

<script>
export default {
  name: 'ElTableWrapColumn',
  props: {
    column: { type: Object, default: () => ({}) },
  },
  components: {
    ColumnSlot: {
      functional: true,
      props: {
        render: {
          type: Function,
          required: true,
        },
        scope: {
          type: Object,
          default: () => ({}),
        },
        column: {
          type: Object,
          required: true,
        },
      },
      render(h, ctx) {
        return ctx.props.render(h, ctx.props.scope, ctx.props.column)
      },
    },
  },
  computed: {
    columnConfig() {
      if (this.column.config) {
        //å¨columnå¯¹è±¡çconfigä¸æ·»å el-table-columnéç½®
        return this.column.config
      }
      const obj = {} //å¨columnå¯¹è±¡ä¸ç´æ¥æ·»å el-table-columnéç½®
      const keys = Object.keys(this.column)
      for (let key of keys) {
        // æé¤ååä¸ºel-table-columnæéç½®çå±æ§
        if (['key', 'prop', 'label', 'children'].indexOf(key) !== -1) continue
        else obj[key] = this.column[key]
      }
      return obj
    },
  },
}
</script>

